@mixin tile($color) {
	background-color: $color;
	&:hover {
		background-color: $color + 20;
	}
}

// Media queries

$tablet-width: 1100px;
$mobile-width: 650px;

@mixin tablet {
	@media (max-width: $tablet-width) {
		@content;
	}
}

@mixin mobile {
	@media (max-width: $mobile-width) {
		@content;
	}
}
